.content {}

.options {
  text-align: center;
}

.PatternDisplay {}

html {
  height: 100%;
}

body {
  min-height: 100%;
  padding: 0px;
  margin: 0px;
}

.Collapsible {
  width: 100%;

}

.CollapsibleInner {
  padding-left: 16px;
  padding-right: 16px;

}

.App {
  display: grid;
  border: none;
  grid-template-columns: 50% 50%;
  grid-template-rows: 50px 50% auto;
  grid-gap: 10px;
  width: 90vw;
  height: 90vh;
  grid-template-areas:
    "header header"
    "pattern image"
    "pattern settings";

}

.header {
  font-size: 50px;
  font-weight: bolder;
  text-align: center;
  grid-area: header;
}

.pattern {
  grid-area: pattern;
}

.image {
  grid-area: image;
}

.settings {
  grid-area: settings;
}

.SizeOptions {
  width: 100%;
}